"use client";

const pageLinks = [
  { name: "Services", href: "#services" },
  { name: "Projects", href: "#projects" },
  { name: "Testimonials", href: "#testimonials" },
  { name: "Contact", href: "#contact" },
];

const socialLinks = [
  {
    name: "LinkedIn",
    href: "https://www.linkedin.com/company/the-prime-crafters/posts/?feedView=all",
    icon: (
      <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
        <path d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z" />
      </svg>
    ),
  },
  {
    name: "Instagram",
    href: "https://www.instagram.com/theprimecrafters/",
    icon: (
      <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
        <path d="M7 2h10a5 5 0 015 5v10a5 5 0 01-5 5H7a5 5 0 01-5-5V7a5 5 0 015-5zm0 2a3 3 0 00-3 3v10a3 3 0 003 3h10a3 3 0 003-3V7a3 3 0 00-3-3H7zm5 3a5 5 0 110 10 5 5 0 010-10zm0 2a3 3 0 100 6 3 3 0 000-6zm5.5-.75a1.25 1.25 0 110 2.5 1.25 1.25 0 010-2.5z" />
      </svg>
    ),
  },
  {
    name: "GitHub",
    href: "https://github.com/The-Prime-Crafters",
    icon: (
      <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
        <path d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z" />
      </svg>
    ),
  },
];

export default function Footer() {
  const currentYear = new Date().getFullYear();

  return (
    <footer className="bg-[#0a0a0a] relative overflow-hidden">
      {/* Top Border */}
      <div className="h-px bg-gradient-to-r from-transparent via-[#D4AF37] to-transparent" />

      <div className="max-w-7xl mx-auto px-6 lg:px-8">
        {/* Main Footer Content */}
        <div className="py-16 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-12">
          {/* Brand Section */}
          <div>
            <a href="#" className="flex items-center gap-3 mb-6">
              <img src="/logo.png" alt="PrimeCrafters Logo" className="h-10 w-auto" />
              <span className="text-xl font-semibold tracking-wide font-[var(--font-cormorant)]">
                <span className="text-[#f5f5f5]">Prime</span>
                <span className="text-gradient-gold">Crafters</span>
              </span>
            </a>
            <p className="text-[#a3a3a3] mb-6 max-w-sm leading-relaxed">
              Transforming businesses through intelligent AI automation. We
              deliver proven results with cutting-edge technology and dedicated
              expertise.
            </p>
          </div>

          {/* Page Links */}
          <div>
            <div className="grid grid-cols-3 gap-4 place-items-center">
              <ul className="space-y-3 text-center">
                {pageLinks.slice(0, Math.ceil(pageLinks.length / 2)).map((link) => (
                  <li key={link.name}>
                    <a
                      href={link.href}
                      className="text-[#737373] hover:text-[#D4AF37] transition-colors text-sm"
                    >
                      {link.name}
                    </a>
                  </li>
                ))}
              </ul>
              <h4 className="text-[#f5f5f5] font-semibold text-sm uppercase tracking-wider text-center self-center">
                Links
              </h4>
              <ul className="space-y-3 text-center">
                {pageLinks.slice(Math.ceil(pageLinks.length / 2)).map((link) => (
                  <li key={link.name}>
                    <a
                      href={link.href}
                      className="text-[#737373] hover:text-[#D4AF37] transition-colors text-sm"
                    >
                      {link.name}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          </div>

          <div>
            <h4 className="text-[#f5f5f5] font-semibold mb-4 text-sm uppercase tracking-wider">
              Follow Us
            </h4>
            <div className="flex items-center gap-4">
              {socialLinks.map((social) => (
                <a
                  key={social.name}
                  href={social.href}
                  className="w-10 h-10 rounded-lg bg-[#111111] border border-[rgba(255,255,255,0.05)] flex items-center justify-center text-[#737373] hover:text-[#D4AF37] hover:border-[rgba(212,175,55,0.3)] transition-all duration-300"
                  aria-label={social.name}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {social.icon}
                </a>
              ))}
            </div>
          </div>
        </div>

        {/* Bottom Bar */}
        <div className="py-6 border-t border-[rgba(255,255,255,0.05)]">
          <div className="flex flex-col md:flex-row items-center justify-between gap-4">
            <p className="text-[#525252] text-sm">
               {currentYear} PrimeCrafters. All rights reserved.
            </p>
            <div className="flex items-center gap-6">
              <a
                href="#"
                className="text-[#525252] hover:text-[#a3a3a3] text-sm transition-colors"
              >
                Privacy Policy
              </a>
              <a
                href="#"
                className="text-[#525252] hover:text-[#a3a3a3] text-sm transition-colors"
              >
                Terms of Service
              </a>
              <a
                href="#"
                className="text-[#525252] hover:text-[#a3a3a3] text-sm transition-colors"
              >
                Cookies
              </a>
            </div>
          </div>
        </div>
      </div>

      {/* Background Decoration */}
      <div className="absolute bottom-0 left-1/2 -translate-x-1/2 w-[800px] h-[400px] bg-[#D4AF37] rounded-full opacity-[0.01] blur-3xl pointer-events-none" />
    </footer>
  );
}

